  --clr-darktheme-200: #1f212e;
  --clr-darktheme-300: #252a41;
  --clr-darktheme-400: #8b97c6;
  --clr-darktheme-500: white;
  --clr-darktheme-600: #333a57;
  --clr-lighttheme-50: #aeb3cb;
  --clr-lighttheme-100: white;
  --clr-lighttheme-200: #f5f7ff;
  --clr-lighttheme-300: #f0f2fa;
  --clr-lighttheme-400: #63687e;
  --clr-lighttheme-500: #1e202a;
