@media only screen and (min-width: 40em) {
  :root {
    --fz-50: 0.625rem;
    --fz-75: 0.75rem;
    --fz-100: 1rem;
    --fz-200: 1.125rem;
    --fz-250: 1.25rem;
    --fz-300: 1.5rem;
    --fz-400: 1.75rem;
    --fz-500: 1.953rem;
    --fz-600: 2.441rem;
    --fz-650: 2.625rem;
    --fz-700: 3.052rem;
  }
}

:root {
  --font-inter: "Inter", sans-serif;
}

body {
  font-family: var(--font-inter);
}

h1,
h2,
h3,
h4,
h5,
h6 {
